@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
.btn{width:100%;height:3rem;background-color:#a0d9d9;box-shadow:5px 2px 10px 5px #8c693e;border:none;color:#d92b04;font-size:1.25rem;font-weight:bold;display:flex;justify-content:center;align-items:center}

#pad{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(3, 1fr);width:50%;grid-gap:1rem;gap:1rem}

#soundName{display:flex;flex-direction:column;justify-content:center;align-items:center}

#drum-machine{display:flex;flex-direction:row;justify-content:center;align-items:center;grid-column-gap:2rem;column-gap:2rem;width:80%;height:50vh;background-color:#f7f7f7;margin:auto;margin-top:10vh;border:2px solid #d92b04;border-radius:15px}@media (max-width: 799px){#drum-machine{flex-direction:column}}

body{background-color:#45858c;font-family:"Poppins", sans-serif}

